* {
  box-sizing: border-box;
  font-family: "'Franklin Gothic Medium'" , "'Arial Narrow'" , Arial , sans-serif;
  margin: 0;
  padding: 0;
}

textarea {
  resize: none;
}

select {
  cursor: pointer;
}

.gameResults button {
  font-size: 1.2rem;
  padding: 10px;
}

.carta {
  align-items: center;
  background-color: black;
  border: 1px solid black;
  border-radius: 15px;
  color: white;
  display: flex;
  flex-direction: column;
  height: 100%;
  position: relative;
  width: 20em;
}

.criacaoDaCarta {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
  margin-top: 24px;
}

.criacaoDaCarta .carta {
  position: relative;
}

.formulario h1 {
  text-align: center;
}

.criacaoDaCarta .formulario {
  align-self: center;
  display: flex;
  flex-direction: column;
}

.criacaoDaCarta .formulario form {
  align-items: center;
  display: flex;
  flex-flow: column;
  gap: 8px;
}

.formInput {
  font-size: 1rem;
  padding: 5px;
  width: 20em;
}

.pontos {
  align-items: flex-start;
  display: flex;
  flex-flow: column wrap;
  gap: 7px;
}

.pontos .pointsQuant {
  align-self: center;
  padding-top: 5px;
}


.superTrunfoCheckbox label {
  align-items: center;
  display: flex;
  font-size: 1.2rem;
  justify-content: center;
}


.superTrunfoCheckbox label span {
  padding: 5px;
}

.botaoSalvar {
  align-self: center;
  cursor: pointer;
  height: 2.5em;
  width: 7em;
}

.nomeCartaContainer {
  align-items: center;
  display: flex;
  height: 5em;
  justify-content: center;
  overflow-wrap: anywhere;
}

.nomeCartaContainer h1 {
  font-size: 1.4rem;
  padding: 5px;
  text-align: center;
}

.imagemCartaContainer {
  background-color: white;
  border: 2px solid white;
  display: flex;
  width: 90%;
}

.carta img {
  height: 250px;
  width: 100%;
}

.cardDescContainer {
  align-items: center;
  border: 1px solid white;
  display: flex;
  height: 5em;
  justify-content: center;
  margin-bottom: 8px;
  margin-top: 8px;
  overflow-wrap: anywhere;
  overflow-y: hidden;
  width: 80%;
}

.cardDesc {
  padding: 5px;
}

.cardAttrContaineer {
  margin-bottom: 18px;
}

.raridade {
  bottom: 0.3em;
  position: absolute;
  right: 0.5em;
}

.superTrunfo {
  bottom: 0.3em;
  left: 0.5em;
  position: absolute;
}

.listaDeCartasTitulo {
  display: flex;
  justify-content: center;
}

.btnsContainer {
  display: flex;
  flex-direction: column;
  gap: 12px;
}

.btnsContainer button {
  font-size: 1.3rem;
  padding: 10px;
  width: 10em;
}


.listaDeCartas {
  display: flex;
  flex-wrap: wrap;
  gap: 18px;
  justify-content: center;
  margin-bottom: 12px;
}

.listaDeCartas .cartaContainer {
  display: flex;
  flex-direction: column;
  gap: 6px;
  justify-content: center;
  position: relative;
}

.listaDeCartas .cartaContainer button {
  align-self: center;
  font-size: 1rem;
  padding: 10px;
  width: 10em;
}

.deleteBtnContainer {
  display: flex;
  position: absolute;
  right: 1em;
  top: 0;
  width: 1em;
}

.deleteBtn {
  align-items: center;
  background-color: red;
  border: 1px solid black;
  border-radius: 100%;
  color: white;
  cursor: pointer;
  display: flex;
  font-weight: 700;
  height: 2em;
  justify-content: center;
}

.deleteBtn:disabled {
  cursor: auto;
}

.filtersContainer {
  align-items: center;
  display: flex;
  flex-direction: column;
  gap: 12px;
  justify-content: center;
  margin-bottom: 30px;
  margin-top: 3em;
}

.filters {
  align-items: center;
  display: flex;
  gap: 12px;
}

.filtroSuperTrunfo {
  display: flex;
  gap: 4px;
}

.filterInput {
  font-size: 0.9rem;
  height: 3em;
  padding: 5px;
  width: 15em;
}

.trunfo {
  cursor: pointer;
}

.deckContainer {
  align-items: center;
  display: flex;
  flex-direction: column;
  gap: 28px;
  margin-bottom: 3.5em;
  margin-top: 4.5em;
}

.deckContainer .deckTitle {
  font-size: 2.3rem;
}

.deckCard {
  align-items: center;
  display: flex;
  flex-direction: column;
}

.removeDeckBtn {
  background-color: red;
  border: 1px solid black;
  color: white;
  margin-top: -8px;
  padding: 10px;
  width: 10em;
  z-index: 5;
}

.playerDeck {
  display: flex;
  flex-flow: row wrap;
  gap: 28px;
  justify-content: center;
}

.playBtnContainer {
  display: flex;
  justify-content: center;
  margin-top: 23px;
}

.playBtn {
  font-size: 1.2rem;
  padding: 10px;
  width: 10em;
}

.chooseCard {
  align-items: center;
  display: flex;
  flex-direction: column;
  gap: 12px;
  margin-bottom: 30px;
  margin-top: 30px;
}

.chooseCard h2 {
  font-size: 2rem;
}

.chooseBtn {
  margin-top: 8px;
  padding: 8px;
  width: 10em;
}

.attrChoose {
  align-items: center;
  display: flex;
  flex-direction: column;
  gap: 12px;
  margin-top: 5em;
}

.compareContainer {
  align-items: center;
  display: flex;
  flex-direction: column;
  gap: 24px;
  margin-bottom: 20px;
  margin-top: 40px;
}

.compareCard {
  display: flex;
  flex-flow: row wrap;
  gap: 12px;
  justify-content: center;

}

.comparePlayAgainBtn {
  font-size: 1rem;
  padding: 10px;
  width: 10em;
}

.gameResults {
  align-items: center;
  display: flex;
  flex-direction: column;
  gap: 12px;
  margin-top: 100px;
}

.gameResults h1 {
  font-size: 2.5rem;
  text-align: center;
}


@media ( max-width : 700px ) {

  .criacaoDaCarta {
    gap: 16px;
  }

  .filters {
    flex-direction: column;
  }
}
